
.bg-skyDark {
  background-color: #0c1f29;
}

.bg-skyMid {
  background-color: #4b6c85;
}

.bg-skyLight {
  background-color: #99b4c3;
}

.animate-twinkle {
  animation: twinkle 0.1s infinite;
}

@keyframes twinkle {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.3;
  }
}


.animate-flicker {
  animation: flicker 2ms infinite;
}

@keyframes flicker {
  0%, 100% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
}


.bg-brown-800 {
  background-color: #6e4b3e;
}


.triangle-light {
  position: absolute;
  top: 100%; 
  width: 0;
  height: 0;
  border-left: 150px solid transparent;
  border-right: 150px solid transparent;
  border-bottom: 300px solid rgba(255, 255, 150, 0.3); 
  transform: translateY(-50%);
  filter: blur(20px);
  opacity: 0.5;
  pointer-events: none;
}
